import './App.css';
import {Dropdown, Modal, Button, Space} from 'antd';
import {useState} from 'react';

function App() {
    const [open, setOpen] = useState(false);
    return (<Space direction="vertical">
        <div>
            1.Dropdown<br/>2.Dropdown<br/>3.Dropdown
        </div>
        <Dropdown trigger="click" dropdownRender={() => {
            return <div style={{
                background: '#FFFFFF', width: '200px'
            }}>
                DropdownDropdownDropdownDropdownDropdownDropdownDropdownDropdown
                <Button onClick={() => {
                    setOpen(true);
                }}></Button>
            </div>
        }}>
            <Button>
                
                <Modal open={open} onCancel={() => {
                    setOpen(false);
                }}>
                    
                </Modal>
            </Button>

        </Dropdown>
    </Space>);
}

export default App;
